/* Main SCSS File */
html {
  font: normal normal normal 1rem/1.5 Georgia, Helvetica Neue, Helvetica, Arial, sans-serif;
}

.vertical-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

button {
  display: block;
  margin: 20px;
  border: 1px solid #505050;
  font-size: 1.5rem;
  padding: 0.6666666667rem;
  border-radius: 3px;
  background: linear-gradient(to bottom, green, white);
  transform: scale(0.5);
}

body {
  font-family: Georgia, Helvetica Neue, Helvetica, Arial, sans-serif;
}
body ul li {
  margin-bottom: 20px;
  margin-left: 20px;
  margin-right: 20px;
  align-items: center;
}
body ul li span {
  font-size: 14px;
  color: #505050;
  text-align: center;
}
body ul li img {
  margin: 5px;
  border-radius: 3px;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.2);
  display: block;
}

.section {
  width: 70%;
  max-width: 780px;
  margin: 0 auto;
  padding: 30px;
  border: 1px solid lightgrey;
  background-color: #cafafa;
}

h1 {
  text-align: center;
}